<template>
    <div class="date-time-box" v-if="isShow">
        <div class="date-box">
            <LeetxDate :ref="date" :date="sonDate"></LeetxDate>
        </div>
        <div class="time-box">
            <LeetxTime :ref="time" :time="sonTime"></LeetxTime>
        </div>
        <LeetxButton class="cancel-btn" :text="$t('confirm.cancel')" fontSize="26px" @click="cancel">
        </LeetxButton>
        <LeetxButton class="btn" :text="$t('confirm.validity')" fontSize="26px" textColor="#fff" bgcColor="#00c155" @click="submitDateTime">
        </LeetxButton>
    </div>
</template>
<script>
// import {maskApi} from '../../dialogs/mask/StackMask.vue'
import LeetxButton from '../controls/LeetxButton.vue'
import LeetxDate from './LeetxDate.vue'
import LeetxTime from './LeetxTime.vue'
// import { vuexStore } from '@/store'
export default {
    components:{
        LeetxDate,
        LeetxTime,
        LeetxButton
    },
    data() {
        return {
            date:`date-${Math.random().toFixed(2)*100}`,
            time:`time${Math.random().toFixed(2)*100}`,
            isShow:true,
            sonDate:undefined,
            sonTime:undefined,
            //当前盒子 class="date-time-box"
            box:undefined,
            // maskId:0,
        }
    },
    methods: {
        // submitCurrentTime(){
        //     let time=new Date(vuexStore.state.g_status.heartbit)
        //     this.sonDate={ 
        //             year:time.getFullYear(),
        //             month:time.getMonth()+1,
        //             day:time.getDate()
        //         },
        //     this.sonTime={
        //             hour:time.getHours(),
        //             min:time.getMinutes(),
        //             sec:time.getSeconds()
        //         }
        // },
        cancel(){
            this.isShow=false
        },
        submitDateTime(){
            let date=this.$refs[this.date].sentDate()
            let time=this.$refs[this.time].sentTime()
            this.onConfirm && this.onConfirm(Object.assign({},date,time))
            this.closeBox()
        },
        closeBox(){
            // closeMask()
            // maskApi.descend(this.maskId)
            this.isShow=false
            this.onCancel && this.onCancel()
        },
        initStatus(){
            // this.maskId=maskApi.rise(33,this.closeBox)
        },
        initDateTime(){
            this.sonDate=this.dateObj
            this.sonTime=this.timeObj
        },
        closeboard(e) {
            let x = e.pageX
            let y = e.pageY
            //日期盒子不存在就再获取一下日期盒子
            if(!this.box){
                this.getBox()
            }
            //获取日期盒子的位置
            let boxPstn=this.box.getBoundingClientRect()
            //盒子坐标
            let topY=boxPstn.top
            let bottomY=boxPstn.bottom
            let leftX=boxPstn.left
            let rightX=boxPstn.right
            //判断鼠标点击位置的四个方向是否超出盒子范围，超出则关闭日期盒子
            if( x<leftX || x>rightX || y<topY || y>bottomY ){
                //移除点击事件监听器
                window.removeEventListener('click',this.closeboard)
                this.closeBox()
            }
        },
        getBox(){
            this.box=document.getElementsByClassName("date-time-box")[0]
        },
        // 是
    },
    created() {
        this.initDateTime()
    },
    mounted() {
        this.initStatus()
        setTimeout(()=>{
            this.getBox()
            window.addEventListener('click', this.closeboard)
        }, 50)
    },
    props:{
        dateObj:{
            type:Object,
            default:function(){
                return  {
                    year:new Date().getFullYear(),
                    month:new Date().getMonth()+1,
                    day:new Date().getDate()
                }
            }
        },
        topY: {
            type: String,
            default: "0rem"
        },
        timeObj:{
            type:Object,
            default:function(){
                return {
                    hour:new Date().getHours(),
                    min:new Date().getMinutes(),
                    sec:new Date().getSeconds()
                }
            }
        }
    },
    watch:{
        dateObj:{
            deep:true,
            handler:function(){
                this.initDateTime()
            }
        },
        timeObj:{
            deep:true,
            handler:function(){
            this.initDateTime()
        }}
    }
}
</script>
<style scoped>
.date-time-box{
    width: 37.5rem;
    height: 47.5rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: v-bind(topY);
    background-color: var(--fff);
    border-radius: 6px;
    z-index: 59;
    border-radius: 6px;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.3);
    margin-bottom: 0.4rem;
}
.activeClickBox{
    color: red;
}
.date-box{
    position: relative;
}

.time-box{
    position: relative;
    top: 0.7rem;
}
.cancel-btn{
    text-align: center;
    position: absolute;
    top: 88%;
    width: 8.625rem;
    height: 3.125rem;
    line-height: 3.125rem;
    left: 7.4375rem;
    background-color: var(--Cee);
    box-shadow: 0px 4px 4px 0px rgb(0 0 0 / 25%);
    border-radius: 0.25rem;
}
.btn{
    text-align: center;
    position: absolute;
    top: 88%;
    width: 8.625rem;
    height: 3.125rem;
    line-height: 3.125rem;
    right: 7.4375rem;
    background-color: var(--C00c155);
    /* font-size:2rem!important; */
    color: var(--fff);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border-radius:0.25rem ;
}


</style>