<template>
    <div class="box">
        <input type="text" v-model="dateTime" @click="showCalendar" readonly>
    </div>
</template>
<script>
import getDatetime from "../../components/date_time/datetime.js"
export default {
    props: {
        width: {
            type: String,
            default: "17.625rem",
        },
        text: {
            type: String,
            // default:(new Date().toLocaleString())
            default: "xxxxxxxxxxxxx"
        },
        sbmitDataTimes: {
            type: Number,
            default: 0
        },
        module:{
            type: String,
            default: "NULL"
        }
    },
    data() {
        return {
            isShow: false,
            valid: true,
            year: undefined,
            mon: undefined,
            day: undefined,
            hour: undefined,
            min: undefined,
            sec: undefined,
            active: true,
            times: 0,
            hasVal: false,
            isActiveSpan:false,
            // 数字键盘的唯一性(保证点击输入框只调用一次数字键盘)
            hasDataKeyboard: false,
        }
    },
    methods: {
        showCalendar(e) {
            let inputVal = e.target.getBoundingClientRect()
            let topY=inputVal.bottom
            topY += "px"
            if(this.hasDataKeyboard) return
            this.hasDataKeyboard = true
            this.isActiveSpan=true
            // 向日历传递日期时间
            if (this.hasVal == false) {
                this.initDateTime()
            }
            let year = this.year
            // year=isNaN(year)?new Date().getFullYear():year
            let month = this.mon
            // month=isNaN(month)?new Date().getMonth()+1:month
            let day = this.day
            // day=isNaN(day)?new Date().getDate():day
            let hour = this.hour
            // hour=isNaN(hour)?new Date().getHours():hour
            let min = this.min
            // min=isNaN(min)?new Date().getMinutes():min
            let sec = this.sec
            // sec=isNaN(sec)?new Date().getSeconds():sec
            let dateObj = { year, month, day }
            let timeObj = { hour, min, sec }
            // (dateObj,timeObj);
            getDatetime({ dateObj, timeObj,topY}).then((result) => {
                // 更新日期时间
                this.updateDateTime(result);
                this.hasVal = true;
                this.sendDateTime();
                this.isActiveSpan=false
                this.hasDataKeyboard = false
            }).catch(() => {
                // this.initDateTime()
                this.isActiveSpan=false
                this.hasDataKeyboard = false

            });
        },
        // 通过heartbit初始化时间
        initDateTime() {
            let times = this.$store.state.g_status.heartbit;
            let date = new Date(times)
            this.year = date.getFullYear()
            this.mon = date.getMonth() +1
            this.day = date.getDate()
            this.hour = date.getHours()
            this.min = date.getMinutes()
            this.sec = date.getSeconds()
        },

        updateDateTime(dateTime) {
            this.year = dateTime.year
            this.mon = dateTime.month 
            this.day = dateTime.day
            this.hour = dateTime.hour
            this.min = dateTime.min
            this.sec = dateTime.sec
            // this.restartDateTime()
        },

        sendDateTime() {
            this.$emit("updateDateTime", {
                text: this.dateTime,
                valid: this.valid,
            });
        },


    },
    computed: {
        dateTime() {
            let date_time = this.hasVal ? new Date(this.year, this.mon-1, this.day, this.hour, this.min, this.sec, 0) : new Date(this.$store.state.g_status.heartbit);   
            var timeString =`${date_time.getFullYear()}-${(date_time.getMonth()+1).toString().padStart(2, '0')}-${date_time.getDate().toString().padStart(2, '0')} ${date_time.getHours().toString().padStart(2, '0')}:${date_time.getMinutes().toString().padStart(2, '0')}:${date_time.getSeconds().toString().padStart(2, '0')}`
            // console.log(timeString)
            // return date_time.toLocaleString("zh-CN", {
            //     hourCycle: "h23",
            //     year: 'numeric',
            //     month: '2-digit',
            //     day: '2-digit',
            //     hour: '2-digit',
            //     minute: '2-digit',
            //     second: '2-digit',
            // });
            return timeString
        },
        enabled() {
            if (this.module == "NULL")
                return true
            else if (this.module in this.$store.state.f_status.permissions && this.$store.state.f_status.permissions[this.module] == 4)
                return true
            else
                return false
        },
        pointerEvent() {
            return this.enabled ? "auto" : "none";
        },
        opacity() {
            return this.enabled ? 1 : 0.5;
        },
    },

    watch: {
        sbmitDataTimes: {
            handler: function () {
                this.sendDateTime()
            }
        }
    },
    unmounted() {
        // this.killDateTime()
    },
    created() {
        this.initDateTime()
        // this.restartDateTime()
    },
    mounted() {
        this.initDateTime()

    },
}
</script>
<style scoped>
.box {
    height: 3.125rem;
    position: relative;
    width: v-bind(width);
    border-radius: 4px;
    /* border: 1px solid #656565; */
    pointer-events: v-bind(pointerEvent);
    opacity: v-bind(opacity);
}

input {
    position: relative;
    width: 100%;
    height: 3.125rem;
    font-family: var(--FROBOT);
    font-size: var(--font1_625);
    text-align: center;
    border-radius: 4px;
    box-sizing: border-box;
    border: 1px solid #656565;
    outline: none;
    font-weight: 500;
    letter-spacing: 0px;
    color: var(--C65) !important;
}

input:focus {
    color: var(--C00c155)!important;
    border: 2px solid var(--C00c155);
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.3);
}
</style>