<template>
    <div class="base-box">
        <div class="device-name-line">
            <span class="device-name">{{ $t("setbase.devicename") }}</span>
            <InputText :module="module" class="name-input" v-model:text="newDevcieData.device_name">
            </InputText>
        </div>
        <div class="device-num-line">
            <span class="device-num">{{ $t("setbase.devicenumber") }}</span>
            <InputVal :module="module" class="num-input" v-model:sensorValue="newDevcieData.device_id" :isInt="true"
                minNum=0 maxNum=65535></InputVal>
        </div>
        <div class="date-time-line">
            <span class="date-time">{{ $t("setbase.time") }}</span>
            <!-- <div class="datetime-input"> -->
            <InputDateTime :module="module" class="datetime-input" @updateDateTime="setSysTime($event)"
                :sbmitDataTimes="updateDateTimes"></InputDateTime>
            <!-- </div> -->
        </div>
        <div class="language-line">
            <span class="lang">{{ $t("setbase.language") }}</span>
            <NewSelect :module="module" :realVal="lang" class="lang-input" :options="{ '中文': 'zhCn', 'English': 'en' }"
                @valChng="changeLang">
            </NewSelect>
        </div>
        <div class="torque-unit-line">
            <span class="torque-unit">{{ $t("setbase.torqueunit") }}</span>
            <NewSelect :module="module" class="torque-input" :isReturnKey="true" :realVal="torqueUnit"
                :isAllowChng="this.$store.state.g_status.busy == false" :options="units.sensors_gains.torque"
                @valChng="updateUnit($event, 'torque')"></NewSelect>
        </div>
        <div class="speed-unit-line">
            <span class="speed-unit">{{ $t("setbase.speedunit") }}</span>
            <NewSelect :module="module" class="speed-input" :isReturnKey="true" :realVal="angleSpeedUnit"
                :isAllowChng="this.$store.state.g_status.busy == false" :options="units.sensors_gains.angle_speed"
                @valChng="updateUnit($event, 'angle_speed')"></NewSelect>
        </div>
        <div class="angle-unit-line">
            <span class="angle-unit">{{ $t("setbase.angleunit") }}</span>
            <NewSelect :module="module" class="angle-input" :realVal="angleUnit" :isReturnKey="true"
                :isAllowChng="this.$store.state.g_status.busy == false" :options="units.sensors_gains.angle"
                @valChng="updateUnit($event, 'angle')"></NewSelect>
        </div>
        <div class="max-torque-line">
            <span class="version-text device-num">{{ $t('setbase.maxTorque') }}(N.m)</span>
            <span class="version-val device-val">{{ this.$store.state.g_status.sensor_max }}</span>
        </div>
        <div class="max-speed-line">
            <span class="version-text device-num">{{ $t('setbase.maxSpeed') }}(r/min)</span>
            <span class="version-val device-val">{{ (oldDeviceData.max_speed * 60).toFixed(3) }}</span>
        </div>
        <div class="embedded-line">
            <span class="version-text embedded-text">{{ $t('setbase.embeddedSoftwareVersion') }}</span>
            <div class="version-val embedded-val">{{ softwareVersion.em_softver }}</div>
        </div>
        <div class="front-line">
            <span class="version-text front-text">{{ $t('setbase.frontSoftwareVersion') }}</span>
            <div class="version-val front-val">{{ webver }}</div>
        </div>
        <div class="server-line">
            <span class="version-text server-text">{{ $t('setbase.backgroundSoftwareVersion') }}</span>
            <div class="version-val server-val">{{ softwareVersion.webapiver }}</div>
        </div>

        <LeetxButton :enablerun="false" :module="module" class="confirm-box" textColor="#fff" bgcColor="#00c155"
            width="30.125rem" height="3.75rem" fontSize="2.25rem" fontFamily="--SHSC-B" :text="confirmMsg"
            @click="submitDevdata">
        </LeetxButton>
    </div>
</template>
<script>
// cnfrmWin
import { devCfgBaseInfoSetApi, devCfgBaseInfoGetApi, devVerApi, devCfgSetSysTimeApi } from "@/api/device/device.js"
import InputText from "../input/InputText.vue"

import InputVal from '../input/InputVal.vue'

// 角度单位
import sensorUnit from "@/utils/sensorUnit";
import InputDateTime from "../input/InputDateTime.vue"
import NewSelect from '../select/NewSelect.vue'
import { setLang, setSensorUnit } from "@/store/localStorage.js";
import { messageApi } from '../message/message.js'
import { deepCopy } from '@/utils/deepCopy.js'
import cnfrmWin from '@/dialogs/common/confirm/cnfrm.js'
import langvue from '@/plugins/langVue.js'
import LeetxButton from "../controls/LeetxButton.vue";


export default {
    components: {
        InputText,
        LeetxButton,
        InputDateTime,
        NewSelect,
        InputVal
    },
    data() {
        return {
            module: "DEV_CONFIG",
            lang: "zhCn",
            webver: '1.0.13',
            newDevcieData: {
                "device_name": "****",
                "device_id": 1,
                "device_time": '2020-01-01 01:01:01',
                "max_speed": 0.1,

            },
            oldDeviceData: {
                "device_name": "****",
                "device_id": 1,
                "device_time": '2020-01-01 01:01:01',
                "max_speed": 0.1,
                isinit: false,
            },
            units: sensorUnit,
            torqueUnit: 1,
            angleUnit: 1,
            angleSpeedUnit: 1,
            isInit: false,
            updateDateTimes: 0,
            softwareVersion: {
                em_hardver: '****',
                em_softver: '****',
                webapiver: '****',
                hand_version: '****',
                mcu_version: '****',
                fpga_version: '****',
            },
            isAllowLeave: true,
            timeChange: false,
        }
    },

    methods: {
        getOtherPars() {
            return `${this.$store.state.g_status.spindle_tranlationratio.toFixed(2)}/${this.$store.state.g_status.trqsensorcalib_normaltorque.toFixed(2)}/${this.$store.state.g_status.trqsensorcalib_sensitivity.toFixed(2)}`
        },
        changeLang(lang) {
            setLang(this.$i18n, this.$global_var, lang);
        },
        initLang() {
            this.lang = this.$global_var.lang == "zhCn" ? "zhCn" : "en";
        },
        submitDevdata() {
            let setData = this.getToServerData()
            let msgOne = langvue.global.t('setbase.success')
            let msgTwo = langvue.global.t('setbase.err')
            devCfgBaseInfoSetApi(setData).then(() => {
                messageApi.success([msgOne])
                this.oldDeviceData.device_name = this.newDevcieData.device_name;
                this.oldDeviceData.device_id = this.newDevcieData.device_id;
                this.oldDeviceData.max_speed = this.newDevcieData.max_speed;
                this.isAllowLeave = true
            }).catch(() => {
                messageApi.error([msgTwo])
            });
        },
        initUnits() {
            this.angleSpeedUnit = this.$store.state.sensors.angle_speed.name;
            this.torqueUnit = this.$store.state.sensors.torque.name;
            this.angleUnit = this.$store.state.sensors.angle.name;
        },
        updateUnit(e, param) {
            // if (this.isInit == false) {
            //     return;
            // }
            setSensorUnit(param, e)
        },
        sensorUnit(uname) {
            return this.$store.state.sensors[uname].name
        },
        getDevData() {
            devCfgBaseInfoGetApi().then((result) => {
                if (result.status == 0) {
                    this.newDevcieData = result.data
                    this.oldDeviceData = deepCopy(result.data)
                    this.oldDeviceData.isinit = true;
                }
                else {
                    this.oldDeviceData.isinit = false;
                }

            }).catch(() => {
                this.oldDeviceData.isinit = false;
            });
        },
        getUnit(name) {
            return this.$store.state.sensors[name]
        },
        getVersion() {
            devVerApi().then((result) => {
                this.softwareVersion = result.data
            })
        },
        //获取要发送给后端的数据
        getToServerData() {
            let serverData = {
                "device_name": this.newDevcieData.device_name,
                "device_id": this.newDevcieData.device_id,
                "max_speed": this.newDevcieData.max_speed,
                // "device_time": this.newDevcieData.device_time.text
            }
            return serverData
        },
        checkDataChange() {
            return this.oldDeviceData.isinit == false || (this.newDevcieData.device_name == this.oldDeviceData.device_name &&
                this.newDevcieData.device_id == this.oldDeviceData.device_id &&
                this.newDevcieData.max_speed == this.oldDeviceData.max_speed)
        },
        setSysTime(e) {
            // this.timeChange = true;
            this.newDevcieData.device_time = e;
            devCfgSetSysTimeApi(e.text)
        }

    },
    created() {
        this.getDevData()
        this.getVersion()
        this.initLang()
        this.initUnits();
        this.webver = process.env.VUE_APP_VERSION;      
    },
    mounted() {
        //setTimeout(() => {
        this.angleSpeedUnit = this.$store.state.sensors.angle_speed.name;
        this.isInit = true;
        // }, 300);
    },
    computed: {
        otherVersions() {
            return this.softwareVersion.hand_version + '/' + this.softwareVersion.mcu_version + '/' + this.softwareVersion.fpga_version
        },
        confirmMsg() {
            return this.$t('confirm.confirm')
        },
        maxInputSpeed() {
            let max = this.$store.state.g_status.speed_max
            return (isNaN(max) ? 60 : max)
        },

    },
    beforeRouteLeave(to, from, next) {
        if (this.checkDataChange() || this.$store.state.g_status.busy) {
            next()
            return;
        }
        let title = this.$t('setcontrol.saveSet')
        let confirmText = this.$t('setcontrol.yes')
        let cancelText = this.$t('setcontrol.no')
        let msg = this.$t('setcontrol.isSaveSet')
        cnfrmWin({
            title,
            confirmText,
            cancelText,
            msg
        }).then(() => {
            this.submitDevdata()
            setTimeout(() => {
                next()
            }, 100)
        }).catch((err) => {
            (err);
            next()
        });
    },
    watch: {
        // newDevcieData: {
        //     deep: true,
        //     // immediate:true,
        //     handler: function () {
        //         if (!this.isFirstChange) {
        //             this.checkDataChange()
        //         }
        //     }
        // }
    }
}
</script>
