<template>
    <div class="calendar-box">
        <div class="date-box">
            <svgloader icon_style="svg-loader" color="black" icon="Double_Left" @click="this.year -= 1"></svgloader>
            <span class="sub-month" @click="subMonth"></span>
            <!-- <span class="date-text" @click="isEditDate=!isEditDate">{{year}}<strong>.</strong>{{showMonth}}</span> -->
            <span class="date-text">{{ year }} - {{ showMonth }}</span>
            <span class="add-month" @click="addMonth"></span>
            <svgloader icon_style="svg-loader-right" color="black" icon="Double_Right" @click="this.year += 1">
            </svgloader>
        </div>
        <table>
            <tr>
                <td><span :class="{ 'fnt3B': firstWeek == 0 }">日</span></td>
                <td><span :class="{ 'fnt3B': firstWeek == 1 }">一</span></td>
                <td><span :class="{ 'fnt3B': firstWeek == 2 }">二</span></td>
                <td><span :class="{ 'fnt3B': firstWeek == 3 }">三</span></td>
                <td><span :class="{ 'fnt3B': firstWeek == 4 }">四</span></td>
                <td><span :class="{ 'fnt3B': firstWeek == 5 }">五</span></td>
                <td><span :class="{ 'fnt3B': firstWeek == 6 }">六</span></td>
            </tr>
            <tr v-for="i of 6" :key="i">
                <td v-for="j of 7" :key="j">
                    <span  :class="dateClass(7 * i + j - 8)"
                        @click="handleDate(7 * i + j - 8)">{{ showDay(7*i+ j - 8) }}</span>
                </td>
            </tr>
        </table>
        <div class="line"></div>
    </div>
</template>
<script>
// import showNumkybrd from '@/keyboard/nbkbrd/nbkybrd'
import svgloader from '@/components/icon_font/svgloader'
export default {
    components: {
        svgloader
    },
    data() {
        return {
            year: (new Date().getFullYear()),
            month: (new Date().getMonth()) + 1,
            days: [],
            day: { status: "current", 'val': (new Date().getDate()) },
            firstWeek: undefined,
            lastDay: undefined,
            activeDay: -1,
            isEditDate: false
        }
    },
    methods: {
        addMonth() {
            if (this.month == 12) {
                this.year += 1
                this.month = 1
            } else {
                this.month += 1
            }
        },
        subMonth() {
            if (this.month == 1) {
                this.year -= 1
                this.month = 12
            } else {
                this.month -= 1
            }
        },
        addYear() {
            this.year += 1
        },
        subYear() {
            this.year -= 1
        },
        // 获取某个月的第一个星期几
        getFirstWeek(year, month) {
            return new Date(year, month - 1, 1).getDay()
        },
        showDay(i) {
            let day = this.days[i]['val']
            return day < 10 ? `0${day}` : `${day}`
        },
        // 获取某个月的最后一天
        getLastDay(year, month) {
            return new Date(year, month, 0).getDate()
        },
        updateTable() {
            this.days = []
            this.firstWeek = this.getFirstWeek(this.year, this.month)
            this.lastDay = this.getLastDay(this.year, this.month)
            let lastMonthLastDay = this.getLastDay(this.year, this.month - 1)
            for (let day = lastMonthLastDay - this.firstWeek + 1; day < lastMonthLastDay + 1; day++) {
                this.days.push({ "val": day, "status": 'last' })
            }
            for (let day = 1; day < this.lastDay + 1; day++) {
                if (day == this.day.val) {
                    this.activeDay = this.days.length
                }
                this.days.push({ "val": day, "status": 'current' })
            }
            this.activeDay = this.days[this.days.length - 1]['val'] >= this.activeDay ? this.activeDay : this.days[this.days.length - 1]['val']
            for (let day = 1; day < 42 - this.lastDay - this.firstWeek + 2; day++) {
                this.days.push({ "val": day, "status": 'next' })
            }
            this.firstWeek = this.activeDay % 7

        },
        initYearMonth() {
            this.year = this.date.year
            this.month = this.date.month
            this.day = { status: "current", 'val': this.date.day }
        },

        handleDate(i) {
            let day = this.days[i]
            this.day = day
            this.activeDay = i
            if (day.status == 'current') {
                this.activeDay = i
            }
            else if (day.status == "last") {
                setTimeout(() => {
                    this.subMonth()
                }, 200)

            } else {
                setTimeout(() => {
                    this.addMonth()
                }, 200)
            }
        },
        dateClass(i) {
            if (this.days[i].status != "current") {

                return 'not-current'
            }
            if (i == this.activeDay) {
                return ['current', 'active-day']
            }
            return 'current'
        },
        sentDate() {
            let date = {}
            date['year'] = this.year
            date['month'] = this.month
            date['day'] = this.day.val
            return date
        },
        checkValidDate() {

        },
        initActiveDay() { },
        // editDate(key) {
        //     this.isEditDate = !this.isEditDate
        //     showNumkybrd({
        //         clbck: key == 'year' ? this.getYear : this.getMonth,
        //         min: 0,
        //         max: key == 'year' ? 2050 : 12,
        //         topY: "27rem",
        //         isInt: true,
        //         val: key == 'year' ? this.year : this.month,
        //         height: '31.25rem',
        //         isLimit: false,
        //         zIndex: 10000,
        //     }).then((result) => {
        //         if (key == 'year') {
        //             this.getYear(result)
        //         } else {
        //             this.getMonth(result)
        //         }
        //     }).catch(() => {
        //     });
        // },
        getYear(year) {
            this.year = parseInt(year)
        },
        getMonth(month) {
            this.month = parseInt(month)
        }


    },
    watch: {
        month() {
            this.updateTable()
        },
        year() {
            this.updateTable()
        },
        date() {
            this.initYearMonth()
        },
    },
    created() {
        this.initYearMonth()
        this.updateTable()
    },
    computed: {
        showMonth() {
            return this.month < 10 ? `0${this.month}` : this.month
        }
    },
    props: {
        clbck: {
            type: Function,
            default: function () {
            }
        },
        date: {
            type: Object,
            default: function () {
                return {
                    year: new Date().getFullYear(),
                    month: new Date().getMonth() + 1,
                    day: { status: "current", 'val': (new Date().getDate()) },
                }
            }
        },
    }

}
</script>
<style scoped>
* {
    margin: 0;
    padding: 0;
}

.svg-loader {
    display: block;
    width: 2rem;
    height: 2rem;
    position: absolute;
    left: calc(50% - 11rem);
    top: 50%;
    transform: translateY(-50%);
}

.svg-loader-right {
    display: block;
    width: 2rem;
    height: 2rem;
    position: absolute;
    right: calc(50% - 11rem);
    top: 50%;
    transform: translateY(-50%);
}

.calendar-box {
    position: relative;
    width: 37.5rem;
    box-sizing: border-box;
    border-radius: 6px 6px 0px 0px;
    overflow: hidden;
}

.date-box {
    position: relative;
    left: 0;
    top: 0;
    width: 37.5rem;
    height: 3.269rem;
    background-color: var(--Cee);

}

.date-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 2.2rem;
    /* border: 1px solid var(--CD4); */
    /* border-radius: 4px; */
}

.add-month {
    display: inline-block;
    width: 10px;
    height: 10px;
    position: absolute;
    left: calc(50% + 6rem);
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
    border-right: 4px solid var(--C65);
    border-top: 4px solid var(--C65);
}

.sub-month {
    display: inline-block;
    width: 10px;
    height: 10px;
    position: absolute;
    left: calc(50% - 7rem);
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
    border-left: 4px solid var(--C65);
    border-bottom: 4px solid var(--C65);
}

button {
    width: 4rem;
    height: 2rem;
}

table {
    position: relative;
    width: 37.5rem;
    height: 25rem;
    display: flex;
    flex-direction: column;
}

table tr {
    flex: 1;
    display: flex;
}

table tr td {
    flex: 1;
    border: 2px solid transparent;
    position: relative;
}

table tr td span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: var(--font1_625);
    height: 2.5rem;
    width: 2.5rem;
    box-sizing: border-box;
    color: var(--C3B);
    text-align: center;
    padding-top: 0.2rem;
    border-radius: 50%;
}

.fnt3B {
    font-weight: 600;
    color: var(--C00) !important;
}

table tr:first-child td span {
    font-size: var(--font1_625);
    color: var(--C96);
}

table tr:nth-child(n+2) td {
    box-sizing: border-box;
    cursor: pointer;
}

table tr:nth-child(n+2) td span:active {
    background-color: var(--C00c155);

}

table tr:nth-child(n+1) td:nth-child(-n+6) {
    margin-right: 2px;
}

table tr:nth-child(-n+7) td {
    margin-bottom: 2px;
}

.current {
    color: black;
}

.not-current {
    color: #c9c4cc;
}

.date-btn {
    position: relative;
    left: 1rem;
}

.active-day {
    color: var(--fff);
    background-color: var(--C00c155);
}

.line {
    width: 33.75rem;
    height: 1px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--Cd4);
}

.san-jiao {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 2.8rem;
    border: 0.625rem solid transparent;
    border-bottom: 0.625rem solid rgba(0, 0, 0, 0.7);


}

.edit-box {
    position: absolute;
    top: 0.525rem;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0.7);
    border-radius: 10px;
    width: 12rem;
    height: 7rem;

}

.edit-box span {
    font-size: 1.8rem;
    display: block;
    width: 100%;
    height: 3.5rem;
    line-height: 3.5rem;
    text-align: center;
    color: var(--fff);
    white-space: nowrap !important;
}

.edit-box span:active {
    color: var(--C00c155);
}
</style>